<!--夜间睡眠-->
<template>
    <!--头部-->
    <div class="SleepNight">
        <s-header :name="'夜间睡眠'"/>
        <p class="top"/>
        <div class="day-night">
            <span>夜间睡眠</span>
            <p class="day-night-duration">
                <span>8</span>
                <span>小时</span>
                <span>0</span>
                <span>分钟</span>
            </p>
            <p class="desc">大部分睡眠质量不好的人或多或少都存在不良的睡眠习惯，如作息不规律、缺乏充足的阳光照射、缺乏足够的体育运动、经常喝咖啡、喝酒、抽烟，或者过度焦虑等。</p>
        </div>
        <sleep-avg-label
                v-for="(item,index) in piedata" :key="index"
                v-show="item.num"
                :title="item.name"
                :timeslot="item.reference"
                :status="item.status"
                :backcolor="'#f2f2f2'"
                :onClickLabel="()=>itemonPress(item)"
        />
    </div>
</template>

<script>
  import sHeader from '@/components/SimpleHeader'
  import {reactive, onMounted, toRefs} from 'vue'
  import SleepDes from '../SleepDes'
  import SleepScore from '../SleepScore'
  import SleepProportion from '../SleepProportion'
  import SleepList from '../SleepList'
  import SleepChart from '../../components/sleep-chart/SleepChart'
  import {useRouter, useRoute} from "vue-router";
  import SleepAvgLabel from '../SleepAvgLabel'

  const {HOST_H5} = require("../../../../utils/axios");
  const {getReferenceResource} = require("../../../../service/sleep");
  import {getUserId} from '@/utils/util'

  export default {
    name: "index",
    components: {
      sHeader,
      SleepAvgLabel,
      // SleepDes,
      // SleepScore,
      // SleepProportion,
      // SleepList,
      // SleepChart,
    },
    setup() {

      const router = useRouter();
      const route = useRoute()
      const flatlist_data = [
        {name: '夜间睡眠', num: 0, unit: '小时', reference: '7-9', status: '', statuscode: -1,},
        {name: '深睡比例', num: 0, unit: '%', reference: '20-60', status: '', statuscode: -1,},
        {name: '浅睡比例', num: 0, unit: '%', reference: '<50', status: '', statuscode: -1,},
        {name: '快速眼动比例', num: 0, unit: '%', reference: '10-30', status: '', statuscode: -1,},
        {name: '深睡连续性', num: 0, unit: '分', reference: '70-100', status: '', statuscode: -1,},
        {name: '清醒次数', num: 0, unit: '次', reference: '0-2', status: '', statuscode: -1,},
      ]

      const setStatus = (piedata, index, min, max) => {
        if (piedata[index].num < min) {
          piedata[index].status = '偏低'
          piedata[index].statuscode = 0
        } else if (piedata[index].num > max) {
          piedata[index].status = '偏高'
          piedata[index].statuscode = 2
        } else {
          piedata[index].status = '正常'
          piedata[index].statuscode = 1

        }
      }

      const initView = (piedata, nightdata) => {
        let nightsleep = 0 //夜间睡眠
        let lightsleep = 0 //浅睡
        let deepsleep = 0  //深睡
        let rem = 0        //眠动
        let deepsleepcontinuity = 0        //深睡连续性
        let wakes = 0        //清醒次数
        let fallsleepduration = 0 //总时长
        const {query} = route
        let data = query.data
        if (data && data) {
          for (let i = 0; i < data.length; i++) {
            if (data[i].sleepType == 0) {
              nightsleep += data[i].fallsleepduration || 0
              lightsleep += data[i].lightsleep || 0
              deepsleep += data[i].deepsleep || 0
              rem += data[i].rem || 0
              deepsleepcontinuity += data[i].deepsleepcontinuity || 0
              wakes += data[i].wakes || 0
              fallsleepduration += data[i].fallsleepduration || 0
            }
          }
          piedata[0].num = parseInt(nightsleep + '')
          piedata[0].reference = nightdata.minSleepHour + '-' + nightdata.maxSleepHour
          setStatus(0, nightdata.minSleepHour * 60, nightdata.maxSleepHour * 60)
          piedata[1].num = parseInt(deepsleep / fallsleepduration * 100 + '')
          setStatus(1, 20, 60)
          piedata[2].num = parseInt(lightsleep / fallsleepduration * 100 + '')
          setStatus(2, 0, 50)
          piedata[3].num = parseInt(rem / fallsleepduration * 100 + '')
          setStatus(3, 10, 30)
          piedata[4].num = parseInt(deepsleepcontinuity / data.length + '')
          setStatus(4, 70, 100)
          piedata[5].num = parseInt(wakes / data.length + '')
          setStatus(5, 0, 2)

          state.piedata = (JSON.parse(JSON.stringify(piedata)))
        }
      }
      const initData = async () => {
        //获取助眠音乐数据
        const res = await getReferenceResource(getUserId())
        if (res.data && res.data.length > 0) {
          initView(flatlist_data, res.data)
        }
      }
      onMounted(
        () => {
          initData()
        }
      )
      const goTo = (r, query) => {
        router.push({path: r, query: query || {}})
      }
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        todo: '',
        selectdate: new Date(),
        piedata: '',
      })
      const toDayNight = () => {
        let url = HOST_H5 + `/sleep/#/dayNight/${'7-9'}/8/0/${1}`;
        goTo('/sleep-night-detail', {url});
      }
        //标签点击跳转h5
      const itemonPress = (item) => {
        if (item.name === '夜间睡眠' || item.name === '平均夜间睡眠') {
          let hour = Math.floor(item.num / 60);
          let minutes = item.num % 60;
          // let statusCode = hour < 6 ? 0 : hour > 10 ? 2 : 1;
          let url = HOST_H5 + `/sleep/#/dayNight/${'7-9'}/${hour}/${minutes}/${item.statuscode}`
          goTo('/sleep-night-detail', {url});

        }
        if (item.name === '深睡比例' || item.name === '平均深睡比例') {
          let url = HOST_H5 + '/sleep/#/deepSleep/' + item.reference + '/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '浅睡比例' || item.name === '平均浅睡比例') {
          let url = HOST_H5 + '/sleep/#/shallowSleep/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '快速眼动比例' || item.name === '平均快速眼动比例') {
          let url = HOST_H5 + '/sleep/#/fastEyesRate/' + item.reference + '/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '深睡连续性' || item.name === '平均深睡连续性') {
          let url = HOST_H5 + '/sleep/#/deepSleepContinuance/' + item.reference + '/' + item.num + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '清醒次数' || item.name === '平均清醒次数') {
          let url = HOST_H5 + '/sleep/#/wakeTimes/' + item.reference + '/' + item.num + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '呼吸质量') {
          let url = HOST_H5 + '/sleep/#/breathQuality/' + item.reference + '/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
      }
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        toDayNight,
        itemonPress,
      }
    }
  }
</script>

