<!--健康监测柱状图-->
<template>
    <div class="div_content">
        <div class="div_v"/>
        <div v-for="(item,index) in sleepData" :key="index">
            <Item :color="data_circledes[item.type].color"
                  :width="widths[index]+'px'"
                  :left="lefts[index]+'px'"
                  :top="data_circledes[item.type].top"
                  :height="'130px'"/>
        </div>

    </div>
</template>

<script>
  import {onMounted, reactive, toRefs} from 'vue'
  // import Item from './Item'


  export default ({
    name: "SleepChart",
    props: {
      data: {
        type: [],
        default: []
      },
    },
    components: {
      // Item
    },
    setup: (props) => {
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        mySliderSource: '',
        sleepData: [],
        villainHeight: 117, //小人上面线高度
        villainLeft: 0, //小人上面线高度left
        itemheight: 13,
        itemspacing: 18,
        chartheight: 130,
        widths: [],
        lefts: [],
        data_circledes: [
          {color: '#9530F2', des: '清醒', type: 0, top: 0},
          {color: '#06B48D', des: '浅睡', type: 1, top: 21},
          {color: '#3168D9', des: '中睡', type: 2, top: 42},
          {color: '#FFBB20', des: '深睡', type: 3, top: 63},
          {color: '#F26230', des: '快速眼动', type: 4, top: 84},
          {color: '#1BC0E4', des: '手动录入', type: 5, top: 105},
          {color: '#D99331', des: '零星小睡', type: 6, top: 105},
        ]
      })

      onMounted(
        () => {
          initsleepData()
          initparam();
        }
      )

      //初始化睡眠数据
      const initsleepData = () => {
        const {data} = props
        let _sleepData = []
        let _data = data.list
        if (_data && _data.length > 0) {
          setinfant(JSON.parse(_data[0].sleepusers)[0].infant)
        }
        for (let i = 0; i < _data.length; i++) {
          let sleepusers = JSON.parse(_data[i].sleepusers)
          for (let j = 0; j < sleepusers.length; j++) {
            //1、未入睡2、浅睡3、中睡4、深睡5、REM（快速眼动）
            switch (sleepusers[j].level) {
              case 1:
                _sleepData.push({
                  color: '#9530F2',
                  des: '清醒',
                  time: 1,
                  type: 0,
                  infant: sleepusers[j].infant,
                  hertz: sleepusers[j].hertz,
                  heartRate: sleepusers[j].heartRate,
                })
                break
              case 2:
                _sleepData.push({
                  color: '#06B48D',
                  des: '浅睡',
                  time: 1,
                  type: 1,
                  infant: sleepusers[j].infant,
                  hertz: sleepusers[j].hertz,
                  heartRate: sleepusers[j].heartRate,
                })
                break
              case 3:
                _sleepData.push({
                  color: '#3168D9',
                  des: '中睡',
                  time: 1,
                  type: 2,
                  infant: sleepusers[j].infant,
                  hertz: sleepusers[j].hertz,
                  heartRate: sleepusers[j].heartRate,
                })

                break
              case 4:
                _sleepData.push({
                  color: '#FFBB20',
                  des: '深睡',
                  time: 1,
                  type: 3,
                  infant: sleepusers[j].infant,
                  hertz: sleepusers[j].hertz,
                  heartRate: sleepusers[j].heartRate,
                })

                break
              case 5:
                _sleepData.push({
                  color: '#F26230',
                  des: '快速眼动',
                  time: 1,
                  type: 4,
                  infant: sleepusers[j].infant,
                  hertz: sleepusers[j].hertz,
                  heartRate: sleepusers[j].heartRate,
                })
                break
            }
          }
        }
        state.sleepData = _sleepData
      }
      //初始化每个显示标签的位置
      const initparam = () => {
        let left = 0
        let sum_time = 0;
        for (let i = 0; i < state.sleepData.length; i++) {
          sum_time += state.sleepData[i].time
        }
        state.sleepData.map((item, index) => {
          let width = item.time / sum_time * 313
          if (index != 0) {
            let width_last = state.sleepData[index - 1].time / sum_time * 313
            left += width_last
          }
          state.widths.push(width)
          state.lefts.push(left)
        })
      }

      //设置小人的图片
      const setinfant = (data) => {
        //0表示右翻身，1表示左翻身 ，2表示上，3表示向下,4坐着
        switch (data) {
          case 0:
            // setmySliderSource(require('../../../../../imgs/ic_people_right.png'))
            break
          case 1:
            // setmySliderSource(require('../../../../../imgs/ic_people_left.png'))
            break
          case 2:
            // setmySliderSource(require('../../../../../imgs/ic_people_right.png'))
            break
          case 3:
            // setmySliderSource(require('../../../../../imgs/ic_people_right.png'))
            break
          case 4:
            // setmySliderSource(require('../../../../../imgs/ic_people_right.png'))
            break
        }
      }

      //记录数据的百分比和距离上面高度，用于设置小人上面线的高度
      let range = [{percentage: 0, top: 0}]
      const pushrange = (percentage, top) => {
        range.push({percentage: percentage, top: top})
      }
      const _setVillainHeight = (percentage) => {
        for (let i = 0; i < range.length - 1; i++) {
          if (percentage > range[i].percentage && percentage < range[i + 1].percentage) {
            state.setVillainHeigh = state.chartheight - range[i + 1].top - state.itemheight - 10
          }
        }
      }
      return {
        ...toRefs(state),
        _setVillainHeight,
        pushrange,
      };
    }
  });
</script>

<style scoped>
    .div_content {
        padding: 0px 30px 0px 30px;
        height: 130px;
        display: flex;
    }

    .div_v {
        height: 100px;
        width: 1px;
        background-color: #E3E3E3
    }

</style>
