<!--健康监测圆环-->
<template>
    <v-chart class="chart" :option="option" />
</template>

<script>
  import { use } from "echarts/core";
  import { CanvasRenderer } from "echarts/renderers";
  import { PieChart } from "echarts/charts";
  import {
    TitleComponent,
    TooltipComponent,
    LegendComponent
  } from "echarts/components";
  import VChart, { THEME_KEY } from "vue-echarts";
  import { ref, defineComponent } from "vue";

  use([
    CanvasRenderer,
    PieChart,
    TitleComponent,
    TooltipComponent,
    LegendComponent
  ]);
  export default defineComponent({
    name: "HelloWorld",
    props: {
      option: { //数据源
        type: Object,
      },
    },
    components: {
      VChart
    },
    provide: {
      [THEME_KEY]: "white"
    },
    setup: () => {
      return {};
    }
  });
</script>

<style scoped>
    .chart {
        height: 130px;
        width: 130px;
    }
</style>
