<!--睡眠比例-->
<template>
    <div class="div_sleepproportion">
        <track-pie :option="option"/>
        <div style="flex: 1;padding-top: 10px">
            <div class="div_item" v-for="(item,index) in _data" :key="index">
                <div class="div_circle" :style="{backgroundColor: item.color}"/>
                <span>{{item.name}}</span>
                <span>{{item.time}}</span>
                <span>{{item.proportion}}</span>
            </div>
        </div>
    </div>
</template>

<script>
  import TrackPie from '../components/TrackPie'
  import {ref, onMounted} from 'vue'

  export default {
    name: "SleepProportion",
    props: {
      data: { //数据源
        type: [],
        default: 0
      },
      option: { //数据源
        type: Object,
      },
    },
    components: {
      TrackPie,
    },
    setup(props) {
      let _data = ref([
        {color: '#9530F2', name: '深睡', time: '0', proportion: 0},
        {color: '#CA31D9', name: '浅睡', time: '0', proportion: 0},
        {color: '#FFBB20', name: '快速眠动', time: '0', proportion: 0},
      ])
      onMounted(
        () => {
          let lightsleep = 0
          let deepsleep = 0
          let rem = 0
          let fallsleepduration = 0
          const {data} = props
          if (data && data.list) {
            for (let i = 0; i < data.list.length; i++) {
              lightsleep += data.list[i].lightsleep || 0
              deepsleep += data.list[i].deepsleep || 0
              rem += data.list[i].rem || 0
              fallsleepduration += parseInt(data.list[i].fallsleepduration || '0')

            }
            _data.value[0].time = parseInt(deepsleep / 60 + '') + '小时' + deepsleep % 60 + '分'
            _data.value[1].time = parseInt(lightsleep / 60 + '') + '小时' + lightsleep % 60 + '分'
            _data.value[2].time = parseInt(rem / 60 + '') + '小时' + rem % 60 + '分'

            if (fallsleepduration == 0) {
              fallsleepduration = 1;
            }
            _data.value[0].proportion = parseInt(deepsleep / fallsleepduration * 100 + '') + '% '
            _data.value[1].proportion = parseInt(lightsleep / fallsleepduration * 100 + '') + '% '
            _data.value[2].proportion = parseInt(rem / fallsleepduration * 100 + '') + '% '
          }
        }
      )
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        _data
      }
    }
  }
</script>

